body {
  margin: 0;
  padding: 0;
  font-family: 'Dancing Script', cursive;
  font-size: 20px;
  background-color: #d9b68e;
  color: #684a0e;
}

h1, h2, h3 {
  margin: 10px 0 10px 0;
}

textarea, input[type=text] {
  font-family: 'Cutive Mono', monospace;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.RecipesList, .DetailedRecipe, .ModalForm {
  background-color: #ebaa18;
  /* margin: 30px; */
  padding: 20px 30px 20px 30px;
  border: 3px solid #684a0e;
  border-radius: 20px;
}

.AppContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.RecipesList {
  width: 200px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-right: 30px;
}

.DetailedRecipe {
  width: 500px;
}

.DetailedRecipeHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.DetailedRecipeHeader > * {
  margin-right: 20px;
}

.DetailedRecipeHeader > button {
  height: auto;
}

.Recipes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  margin: 10px 0;
}

.Recipe {
  border: none;
  background-color: #ebaa18;
  cursor: pointer;
  margin: 10px 0;
  font-family: 'Dancing Script', cursive;
  font-size: 20px;
  color: #684a0e;
  font-weight: 900;
}

.ModalForm {
  z-index: 1010;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
          box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
}

.ModalForm input[type=submit] {
  margin: 10px 0;
}

.ModalOverlay {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.5;
}

.DeleteButton, .EditButton, .AddButton {
  border: none;
  background-color: #ebaa18;
}

.DeleteButtonIcon {
  background: url('./icons/delete.svg');
  background-size: contain;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  width: 25px;
}

.AddButtonIcon {
  background: url('./icons/add.svg');
  background-size: contain;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  width: 25px;
}

.EditButtonIcon {
  background: url('./icons/edit.svg');
  background-size: contain;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  width: 25px;
}



